.vc-form select {
    -webkit-appearance: none;
    -moz-appearance: none;
    text-transform: none;
}

.vc-form .form-group {
    margin: 0 0 10px;
    position: relative;
}

.vc-form .form-label {
    color: #999;
    font-size: 12px;
    opacity: 0;
    padding: 0 11px;
    position: absolute;
    top: 50%;
    transform: translateY(3px);
    transition: all .2s ease-out;
}

.vc-form .form-input {
    background-color: white;
    border: none;
    border-radius: 4px;
    box-shadow: 0 0 0 1px #d9d9d9;
    box-sizing: border-box;
    font-size: 14px;
    margin: 0;
    padding: 13px 11px;
    width: 100%;
    transition: all .2s ease-out;
    outline: none;
}

    .vc-form .form-input:focus {
        box-shadow: 0 0 0 2px #686868;
    }

.vc-form .form-group.keyuped .form-label {
    opacity: 1;
    transform: none;
    top: 5px;
}

.vc-form .form-group.keyuped .form-input {
    padding: 21px 11px 5px;
}

.vc-form .form-group.error .form-input {
    box-shadow: 0 0 0 2px #ff6d6d;
}

.vc-form .form-error {
    background: none;
    border: none;
    color: #ff6d6d;
    display: block;
    font-size: 12px;
    margin: 9px 0 3px;
}

.vc-form .form-select::after {
    border-left: 1px solid #ccc;
    content: ' ';
    height: 20px;
    position: absolute;
    right: 11px;
    top: 50%;
    width: 21px;
    transform: translateY(-50%);
    pointer-events: none;
}

.vc-form .form-select::before {
    border-color: #333 transparent transparent;
    border-style: solid;
    border-width: 5px 5px 0;
    content: ' ';
    position: absolute;
    right: 11px;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
}

.vc-btn {
    background: #cccccc;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
    color: #474747;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    padding: 19px 23px;
    position: relative;
    text-align: center;
    text-decoration: none;
    transition: background-color .2s ease-in-out, color .2s ease-in-out;
}

    .vc-btn.secondary {
        background: #dcdcdc;
        color: #585858;
    }

    .vc-btn.loading {
        pointer-events: none;
    }

        .vc-btn.loading .btn-text {
            opacity: 0;
        }

        .vc-btn.loading .btn-loading {
            border: 3px solid #686868;
            border-top-color: transparent;
            border-radius: 50%;
            box-sizing: border-box;
            height: 30px;
            margin: -15px 0 0 -15px;
            left: 50%;
            position: absolute;
            top: 50%;
            width: 30px;
            -webkit-animation: loading .75s linear infinite;
            animation: loading .75s linear infinite;
        }

@-webkit-keyframes loading {
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes loading {
    100% {
        transform: rotate(360deg);
    }
}

