}

.vc-form .form-box .switch {
    border: 1px solid #ccc;
    border-radius: 50%;
    box-sizing: border-box;
    margin: 0 15px 0 0;
    height: 18px;
    position: relative;
    width: 18px;
}

.vc-form .form-box .radio:checked ~ .switch::after {
    border: 7px solid #686868;
    border-radius: 50%;
    box-sizing: border-box;
    bottom: -1px;
    content: ' ';
    left: -1px;
    position: absolute;
    right: -1px;
    top: -1px;
}

.vc-form .check-box .switch {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    border-radius: 3px;
}

.vc-form .form-box .check:not(:checked) ~ .switch {
    background-color: white;
}

.vc-form .form-box .check:checked ~ .switch {
    background: #1990c6;
    border: 1px solid #686868;
}

    .vc-form .form-box .check:checked ~ .switch::after {
        color: #fff;
        content: 'ï€Œ';
        font-family: 'FontAwesome';
